import Layout from '@/views/layout'

const DynamicRoutes = [
  {
    path: '/',
    component: Layout,
    redirect: 'home',
    name: 'home',
    level: 1,
    meta: {
      title: '视频巡查',
      icon: 'el-icon-camera',
      svgClass: 'menu-视频巡查'
    },
    children: [
      {
        path: 'home',
        name: 'home',
        component: () => import('@/views/Home.vue'),
        meta: {
          title: '视频巡查',
          icon: 'el-icon-camera',
          svgClass: 'menu-视频巡查',
          affix: true
        }
      }
    ]
  }
]

export default DynamicRoutes
