import * as types from '../mutation-types'
import KLStorage from '@/utils/storage'
import API from '@/api'

export default {
  namespaced: true,
  state: {
    token: '',
    added: false
  },
  mutations: {
    [types.TOKEN](state, token) {
      state.token = token
    },
    setEventState(state, value) {
      state.added = value
    }
  },
  actions: {
    userLogin({ commit }, params) {
      return new Promise(resolve => {
        API.login(params)
          .then(res => {
            if (res.status === 200) {
              KLStorage.setItem(types.TOKEN, res.data)
              commit(types.TOKEN, res.data)
              resolve(res.data)
            }
          })
          .catch(err => {
            console.log(err)
          })
      })
    }
  }
}
