<script>
// import API from '@/api/index.js'
// import Person from './components/Person.vue'
export default {
  name: 'Layout',
  // components: {
  //   Person
  // },
  data() {
    return {
      activeName: '/abilityStorage',
      activeRoute: '',
      unreadMessageList: [],
      messageCount: 0,
      leftDevice: 0,
      userInfo: JSON.parse(localStorage.getItem('userInfo')),

      permission: [], // 用户当前权限
      routes: [
        // {
        //   name: '推送管理',
        //   class: 'icon-shijian',
        //   path: '/dataManage/list',
        //   icon1: require('../../assets/img/dataImg1.png'),
        //   icon: require('../../assets/img/dataImg.png'),
        //   isIcon: true,
        //   children: [
        //     {
        //       name: '事件管理',
        //       class: 'icon-shijian',
        //       path: '/dataManage/list'
        //     },
        //     {
        //       name: '推送地址',
        //       class: 'icon-shijian',
        //       path: '/dataManage/push_setting'
        //     }
        //   ]
        // }
      ],
      isCollapse: false
    }
  },
  watch: {
    $route: {
      handler(route) {
        // 设置重定向导航选中
        if (route.meta && route.meta.redirectPath) {
          this.activeRoute = route.meta.redirectPath
        } else {
          this.activeRoute = route.path
        }
      },
      immediate: true
    }
  },
  created() {},
  methods: {
    handleOpen(key) {
      this.routes = this.routes.filter(citem => {
        return (citem.isIcon = true)
      })
      this.routes.forEach(item => {
        if (key == item.path) {
          item.isIcon = false
        }
      })
    },
    handleClose() {
      this.routes = this.routes.filter(citem => {
        return (citem.isIcon = true)
      })
    },
    handleSelect(index) {
      if (index == 'https://www.baidu.com') {
        // window.location.href = index
        window.open(index)
      } else {
        this.$router.push(index)
        if (index == '/navigator') {
          this.routes = this.routes.filter(citem => {
            return (citem.isIcon = true)
          })
          this.routes.forEach(item => {
            if (item.path == '/navigator') {
              item.isIcon = false
            }
          })
        }
      }
    }
  }
}
</script>

