<template>
  <div id="app">
    <router-view />
  </div>
</template>

<style lang="scss">
html,
body,
#app {
  width: 100%;
  height: 100%;
  font-family: PingFang SC;
  scroll-behavior: smooth;
}
// 滚动条样式
::-webkit-scrollbar {
  width: 4px;
  height: 8px;
}
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  background: rgba(233, 226, 226, 0.2);
}
::-webkit-scrollbar-corner {
  display: none;
}
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  border-radius: 0;
  background: transparent;
}
</style>

<script>
export default {
  methods: {
    keyUp(event) {
      if (this.holdTime > 200) {
        this.$notify({
          title: '请勿长按A / D键!',
          message: '如有渲染错误您可以刷新页面',
          type: 'warning',
          duration: 8000
        })
      }
      this.holdTime = 0
      clearInterval(this.holdTimer)
      this.holdedKey = ''
      this.operateType = this.brush == null ? 'free' : 'brush' // 释放状态
      // 按下 A 执行上一张操作
      if (!this.loading && event.code === 'KeyA' && this.datalist.length > 0) {
        // if (this.loading || this.holdTime > 0) {
        //   return false
        // }
        // 已在第一张, 无法再向上
        if (this.currentindex == 0) {
          this.$message.warning('已在最前!')
        } else if (this.currentindex < 0) {
          // 当前在操作getNext的内容, 按下A, 跳转至当前列表最后一个
          this.$emit('update:currentindex', this.datalist.length - 1)
          this.changeImg(this.datalist.length - 1)
        } else {
          // 当前位于队列中, 直接向上翻
          this.$emit('update:currentindex', this.currentindex - 1)
          this.changeImg(this.currentindex - 1)
        }
      }
      // 按下 D 执行下一张操作
      // 这里有隐式触发逻辑, 即当翻到当前页最后一条时需要进行分页查询
      if (!this.loading && event.code === 'KeyD') {
        // if (this.loading || this.holdTime > 0) {
        //   return false
        // }
        // currentindex小于0时说明在操作最新内容, 不在列表内
        if (this.currentindex < 0) {
          // this.$message.warning('已经是最新内容!')
          this.submit(2)
        } else {
          if (this.currentindex < this.datalist.length - 1) {
            // 当前序列依然位于当前可视序列内, 执行+1即可
            this.$emit('update:currentindex', this.currentindex + 1)
            this.changeImg(this.currentindex + 1)
          } else {
            // 当前序列大于了当前可视序列
            if (this.currentindex < this.labelComplete) {
              // 当前还有数据未进行加载, 这里自动加载并更新index
              this.$emit('autoLoadMore')
              // this.$emit('update:currentindex', this.currentindex + 1)
            } else {
              debugger
              // 余下数据也加载完毕, 则拉取最新的getNext
              this.getNext()
            }
          }
        }

        // event.preventDefault()
        // this.submit(2)
        // this.$emit('update:currentindex', this.currentindex + 1)
      }
    }
  }
}
</script>
