import dynamicRoutes from '@/router/dynamicRoutes'
import defaultRoutes from '@/router/defaultRoutes'
import KLStorage from '@/utils/storage'
import * as types from '../mutation-types'
import API from '@/api'

/**
 * 通过路由path判断是否与当前用户权限匹配
 * @param permissionMenu
 * @param route
 */
function hasPermission(permissionMenu, route) {
  const hasRoute = permissionMenu.find(p => p.url === route.name)
  if (hasRoute) {
    const { sort, name } = hasRoute
    route.sort = sort
    route.meta && (route.meta.title = name)
  }
  return (
    permissionMenu.findIndex(v => {
      const path = v.url || v
      return route.name.includes(path)
    }) > -1
  )
}

/**
 * 递归过滤异步路由表，返回符合用户权限的路由表
 * @param routes asyncRouterMap
 * @param permissionMenu
 */
function filterAsyncRouter(routes, permissionMenu) {
  const menu = []
  routes.forEach(route => {
    const tmp = { ...route }
    if (hasPermission(permissionMenu, tmp)) {
      if (tmp.children && tmp.children.length) {
        tmp.children = filterAsyncRouter(route.children, permissionMenu)
      } else {
        tmp.children = null
      }
      menu.push(tmp)
    }
  })
  return menu
}
/**
 *
 * @param {Array} routes 用户过滤后的路由
 *
 * 递归为所有有子路由的路由设置第一个children.path为默认路由
 */
export function setDefaultRoute(routes) {
  routes.forEach(v => {
    if (v.children && v.children.length > 0) {
      v.redirect = { name: v.children[0].name }
      setDefaultRoute(v.children)
    }
  })
}
const permission = {
  namespaced: true,
  state: {
    routes: [],
    addRoutes: [],
    accessRoutes: []
  },
  mutations: {
    [types.SET_PERMISSION]: (state, accessRoutes) => {
      state.accessRoutes = accessRoutes
    },
    [types.SET_ROUTES]: (state, routes) => {
      state.addRoutes = routes
      state.routes = defaultRoutes.concat(routes)
    }
  },
  actions: {
    getUserMenu({ commit }) {
      return new Promise(resolve => {
        API.fetchUserMenuList({ pag: false, type: 1 }).then(res => {
          if (res.status === 200) {
            resolve(res.data)
            commit(types.SET_PERMISSION, res.data)
          }
        })
      })
    },
    resetToken({ commit }) {
      return new Promise(resolve => {
        commit(types.TOKEN, '')
        KLStorage.removeItem(types.TOKEN)
        resolve()
      })
    },
    GenerateRoutes({ commit }, permissionList) {
      return new Promise(resolve => {
        const routes = filterAsyncRouter(dynamicRoutes, permissionList)
        setDefaultRoute(routes)
        routes.push({
          path: '*',
          hidden: true,
          redirect: '/404'
        })
        commit(types.SET_ROUTES, routes)
        resolve(routes)
      })
    },
    // 遍历菜单获取Permission字段
    async GetMeunPermission({ commit }, data) {
      const permissionMenu = []

      function getMenus(menuData) {
        menuData.forEach(e => {
          if (e.permission) {
            // 需要排序字段
            const { permission, sort, name } = e
            const menuItem = { path: permission, sort, name }
            permissionMenu.push(menuItem)
          }
          if (e.children && Array.isArray(e.children)) {
            getMenus(e.children)
          }
        })
      }
      getMenus(data)
      commit(types.SET_PERMISSION, permissionMenu)
      return permissionMenu
    }
  }
}

export default permission
