import Moment from 'moment'

/**
 * 时间格式化
 * @param key
 * @param format
 * @returns {string}
 */
export default {
  formatTypeName(key) {
    if (key) {
      let foramtName = ''
      switch (key) {
        case 'tech':
          foramtName = '技术'
          break
      }
      return foramtName
    }
    return ''
  },
  formatDate(key, format) {
    if (key) {
      return format ? Moment(key).format(format) : Moment(key).format('YYYY-MM-DD HH:mm:ss')
    }
    return ''
  }
}
