function checkAuth(role, vNode) {
  return vNode.context.$route.meta.roles.indexOf(role) !== -1
}

const install = Vue => {
  Vue.directive('permission', {
    inserted(el, binding, vNode) {
      if (binding.arg) {
        if (!checkAuth(binding.arg, vNode)) {
          // 没有权限移除DOM元素
          el.parentNode && el.parentNode.removeChild(el)
        }
      } else {
        console.error(`${el.innerText}元素,需要传入权限Key`)
      }
    }
  })
}

export default install
